import React, { Component } from 'react'
import axios from 'axios';
import swal from 'sweetalert';
import  Button  from 'react-bootstrap/Button';
import { NavLink } from 'react-router-dom';


class EditCustomers extends Component{
    state = {
      customers : [],
      countries: {}
    }
      ChangeDetails = () => {
        if (this.state.customers == undefined) {
          swal('you didnt make any changes')
          return;
        }
          axios.post(`https://localhost:44335/api/Admin/updatecustomerdetails`,
           {
               "Id": this.state.newCustomer.id,
               "First_name" : this.state.newCustomer.first_name,
               "Last_name" : this.state.newCustomer.last_name,
               "Adress" : this.state.newCustomer.adress,
               "Phone" : this.state.newCustomer.phone,
               "Credit_card_no" : this.state.newCustomer.credit_card_no,
               "User_id" : Number(this.state.newCustomer.user_id)
           },
            {
              headers: {
                'Authorization': `Bearer ${localStorage["JWT"]}`
              }
            })
      }

      GetCustomers()
      {
        axios.get("https://localhost:44335/api/Admin/getallcustomers", {
      headers: {
        'Authorization': `Bearer ${localStorage["JWT"]}`
      }
    })
      .then(response => {
        this.setState({
          customers: response.data
        })
      })
      }

      GetCountries() {
        axios.get("https://localhost:44335/api/Country/getcountries", {
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
          .then(response => {
            var obj = {}
            response.data.forEach(country => {
              obj[`${country.id}`] = `${country.name}`
            })
            this.setState(this.state.countries = obj)
          })
          .then(r => {
            this.GetCustomers()
          })
      }

      handleChange = (event) => {

        var customer = this.state.customers.filter(c => c.id == event.target.id)[0]
        var cust = {}
        if (this.state.newCustomer == undefined || this.state.newCustomer.id != customer.id)
        {
          cust = {
            id: customer.id,
            user_id: customer.user_id,
            first_name: customer.first_name,
            last_name: customer.last_name,
            credit_card_no: customer.credit_card_no,
            phone: customer.phone,
            adress: customer.adress
          }
          cust[event.target.name] = event.target.value
        }
        else
        {
          cust = this.state.newCustomer
          cust[event.target.name] = event.target.value
        }
        this.setState({
          newCustomer: cust
        })
      }

      componentDidMount()
      {
          this.GetCountries()
      }
      
        render()
    {
      return(
        <div>
           <nav>
            <div>
                <a>CoolFlightsPlace</a>
                <ul>
                    <li><NavLink to="/AirlineRequests">AirlineRequests</NavLink></li>
                    <li><NavLink to="/EditAirlines">EditAirlines</NavLink></li>
                    <li><NavLink to="/EditCustomers">EditCustomers</NavLink></li>
                </ul>
            </div>
        </nav>
            {this.state.customers.map(request => {
            return(
            <div key={request.id}>
            <form name="details" onSubmit={e => {
                e.preventDefault();
              }}>
              <p>First Name<input defaultValue={request.first_name} type="text" name="first_name" onChange={this.handleChange} id={request.id} required/></p>
              <p>Last Name<input defaultValue={request.last_name} type="text" name="last_name" onChange={this.handleChange} id={request.id} required/></p>
              <p>Adress<input defaultValue={request.adress} type="text" name="adress" onChange={this.handleChange} id={request.id} required/></p>
              <p>Phone<input defaultValue={request.phone} type="number" name="phone" onChange={this.handleChange} id={request.id} required/></p>
              <p>Credit Card Number<input defaultValue={request.credit_card_no} type="number" name="credit_card_no" onChange={this.handleChange} id={request.id} required/></p>
              <p>User ID<input defaultValue={request.user_id} type="number" name="user_id" onChange={this.handleChange} id={request.id} required/></p>
              <Button onClick={this.ChangeDetails}>Edit</Button>
             </form>
             <hr/>
            </div>)
            })}
        </div>
     )
    }
  }
  export default EditCustomers