import React, { Component } from 'react'
import axios from 'axios';
import $ from 'jquery';
import swal from 'sweetalert';
import Button from 'react-bootstrap/Button';
import { NavLink } from 'react-router-dom';


class EditAirlines extends Component {
  state = {
    airlines: [],
    countries: {}
  }
  ChangeDetails = () => {
    if (this.state.newAirline == undefined) {
      swal('you didnt make any changes')
      return;
    }
    if (this.getKeyByValue(this.state.countries, this.state.newAirline.countryID) == undefined) {
      swal('that country is not available')
      return;
    }

    axios.post(`https://localhost:44335/api/Admin/updateairlinedetails`,
      {
        "ID": this.state.newAirline.id,
        "Name": this.state.newAirline.name,
        "CountryID": Number(this.getKeyByValue(this.state.countries, this.state.newAirline.countryID)),
        "UserId": Number(this.state.newAirline.userId)
      },
      {
        headers: {
          'Authorization': `Bearer ${localStorage["JWT"]}`
        }
      })
  }

  GetAirlines() {
    axios.get("https://localhost:44335/api/Admin/getallairlinecompanies", {
      headers: {
        'Authorization': `Bearer ${localStorage["JWT"]}`
      }
    })
      .then(response => {
        this.setState({
          airlines: response.data
        })
      })
  }

  GetCountries() {
    axios.get("https://localhost:44335/api/Country/getcountries", {
      headers: {
        'Authorization': `Bearer ${localStorage["JWT"]}`
      }
    })
      .then(response => {
        var obj = {}
        response.data.forEach(country => {
          obj[`${country.id}`] = `${country.name}`
        })
        this.setState(this.state.countries = obj)
      })
      .then(r => {
        this.GetAirlines()
      })
  }

  getKeyByValue = (object, value) => {
    if (isNaN(value))
      return Object.keys(object).find(key => object[key] === value);
    else {
      return this.state.countries[`${value}`]
    }
  }

  handleChange = (event) => {

    var airline = this.state.airlines.filter(c => c.id == event.target.id)[0]
    var air = {}
    if (this.state.newAirline == undefined || this.state.newAirline.id != airline.id)
    {
      air = {
        id: airline.id,
        name: airline.name,
        countryID: this.getKeyByValue(this.state.countries, airline.countryID),
        userId: Number(airline.userId)
      }
      air[event.target.name] = event.target.value
    }
    else
    {
      air = this.state.newAirline
      air[event.target.name] = event.target.value
    }

    this.setState({
      newAirline: air
    })
  }

  componentDidMount() {
    this.GetCountries()
  }

  render() {
    return (
      <div>
        <nav>
          <div>
            <a>CoolFlightsPlace</a>
            <ul>
              <li><NavLink to="/AirlineRequests">AirlineRequests</NavLink></li>
              <li><NavLink to="/EditAirlines">EditAirlines</NavLink></li>
              <li><NavLink to="/EditCustomers">EditCustomers</NavLink></li>
            </ul>
          </div>
        </nav>
        {this.state.airlines.map(request => {
          return (
            <div key={request.id}>
              <form name="details" onSubmit={e => {
                e.preventDefault();
              }}>
                <p>Name<input defaultValue={request.name} type="text" id={request.id} name='name' onChange={this.handleChange} required /></p>
                <p>Country<input defaultValue={this.state.countries[`${request.countryID}`]} type="text" id={request.id} name='countryID' onChange={this.handleChange} required /></p>
                <p>User ID<input defaultValue={request.userId} type="number" id={request.id} name='userId' onChange={this.handleChange} required /></p>
                <Button onClick={this.ChangeDetails}>Edit</Button>
              </form>
              <hr />
            </div>)
        })}
      </div>
    )
  }
}
export default EditAirlines