import React, { Component } from 'react'
import axios from 'axios';
import $ from 'jquery';
import Button from 'react-bootstrap/Button';
import { NavLink } from 'react-router-dom';



class PersonalDetails extends Component {
  state = {
    initialUserName: "",
    initialPassword: "",
    initialFisrtName: "",
    initialLastName: "",
    initialEmail: "",
    initialPhoneNumber: "",
    initialadress: "",
    initialCreditCardNumber: ""
  }
  ChangeDetails(event) {
    event.preventDefault();
    axios.post(`https://localhost:44335/api/Customer/updatecustomerdetails`,
      {
        "Username": $('#usernameidd').valueOf().val(),
        "Password": $('#passwordidd').valueOf().val(),
        "FisrtName": $('#firstnameid').valueOf().val(),
        "LastName": $('#lastnameid').valueOf().val(),
        "email": $('#emailid').valueOf().val(),
        "PhoneNumber": $('#phonenumid').valueOf().val(),
        "adress": $('#adressid').valueOf().val(),
        "CreditCardNumber": $('#creditcardid').valueOf().val(),
      },
      {
        headers: {
          'Authorization': `Bearer ${localStorage["JWT"]}`
        }
      }).then(r => {
        const obj = {
          Name: $('#usernameidd').valueOf().val(),
          Password: $('#passwordidd').valueOf().val()
      }
      console.log(obj)
        axios.post("https://localhost:44335/api/Auth/token", obj)
          .then(response => {
            localStorage.setItem('JWT', response.data)
          })
      })

  }
  componentDidMount() {
    axios.get("https://localhost:44335/api/Customer/getmydetails", {
      headers: {
        'Authorization': `Bearer ${localStorage["JWT"]}`
      }
    })
      .then(res => {
        this.setState(this.setState({ initialUserName: res.data.username }))
        this.setState(this.setState({ initialPassword: res.data.password }))
        this.setState(this.setState({ initialFisrtName: res.data.fisrtName }))
        this.setState(this.setState({ initialLastName: res.data.lastName }))
        this.setState(this.setState({ initialEmail: res.data.email }))
        this.setState(this.setState({ initialadress: res.data.adress }))
        this.setState(this.setState({ initialCreditCardNumber: res.data.creditCardNumber }))
        this.setState(this.setState({ initialPhoneNumber: res.data.phoneNumber }))

      })
  }




  render() {
    return (
      <div>
        <nav>
          <div>
            <a>CoolFlightsPlace</a>
            <ul>
              <li><NavLink to="/MyTickets">MyTickets</NavLink></li>
              <li><NavLink to="/PersonalDetails">PersonalDetails</NavLink></li>
              <li><NavLink to="/Flights">Flights</NavLink></li>
            </ul>
          </div>
        </nav>
        <form name="details">
          <p>Username<input defaultValue={this.state.initialUserName} type="text" id="usernameidd" required /></p>
          <p>Password<input defaultValue={this.state.initialPassword} type="text" id="passwordidd" required /></p>
          <p>First Name<input defaultValue={this.state.initialFisrtName} type="text" id="firstnameid" required /></p>
          <p>Last Name<input defaultValue={this.state.initialLastName} type="text" id="lastnameid" required /></p>
          <p>Email<input defaultValue={this.state.initialEmail} type="email" id="emailid" required /></p>
          <p>Phone Number<input defaultValue={this.state.initialPhoneNumber} type="number" id="phonenumid" required /></p>
          <p>Adress<input defaultValue={this.state.initialadress} type="text" id="adressid" required /></p>
          <p>Credit Card Number<input defaultValue={this.state.initialCreditCardNumber} type="number" id="creditcardid" required /></p>
          <Button onClick={this.ChangeDetails}>Edit</Button>
        </form>
      </div>
    )
  }
}
export default PersonalDetails